<template>
  <div class="container">
    <div class="registerBox">
      <div class="unLogin">
        <div class="doll"><img src="/img/doll.png" /></div>
        <h2>請修改密碼</h2>
        <div class="formRow">
          <h3>Email</h3>
          <input
            id="email"
            type="email"
            name=""
            value=""
            placeholder="請輸入您的email同時為帳號"
            required
            v-model="User.Email"
          />
        </div>
        <div class="formRow"
            v-if="!User.OpenID">
          <h3>密碼</h3>
          <input
            id="password1"
            type="password"
            name=""
            value=""
            required
            placeholder="請輸入您原來的密碼"
            v-model="User.Password"
          />
        </div>
        <div class="formRow">
          <h3>確認密碼 </h3>
          <input
            id="password2"
            type="password"
            name=""
            value=""
            required
            placeholder="請輸入新密碼"
            v-model="User.Password2"
          />
        </div>
        <div class="formRow">
          <h3>確認密碼 </h3>
          <input
            id="password3"
            type="password"
            name=""
            value=""
            required
            placeholder="請再次輸入新密碼"
            v-model="User.Password3"
          />
        </div>
        <div class="submitBtn">
          <a @click="register" href="javascript:void(0);">修改</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      User: {
        Email: "",
        Password: "",
        Password2: "",
        Password3: "",
      },
    };
  },
  async mounted() {
    this.setBase("register");
    var response = await this.$api.getMe();
    this.User.Email = response.Email;
    this.User.Name = response.Name;
  },
  methods: {
    async register() {
      if (!this.validate(".registerBox")) {
        return;
      }
      if (this.User.Password2 != this.User.Password3) {
        alert("兩次密碼不一致，請重新輸入！");
        return;
      }
      if (
        !/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/.test(
          this.User.Password2
        )
      ) {
        alert("密碼設定需包含8位以上的英文大小寫+數字");
        return;
      }
      try {
        var result = await this.$api.updatePassword({
          Email: this.User.Email,
          OldPassword: this.User.Password,
          NewPassword: this.User.Password2
        });
        alert('修改成功，請用新密碼登入');
        this.$router.replace("/login");
      } catch (e) {
        console.error(e);
        if (e.Status == 4) {
          alert("此帳號已註冊過");
        }
      }
    },
  },
};
</script>